import jsPDF from 'jspdf'


export class MedicalOrderSheet {
    generateFromDict(dictionary)
    {
        let template = this.getTemplate()
        Object.keys(dictionary).forEach(function(key) {
            template = template.replace('{{' + key + '}}', dictionary[key])
        });

        return template
    }

    getTemplate() {
        return `
        <div style="margin:10px">
        <table width="100%" cellspacing="0" cellpadding="0">
            <tr>
                <td><img src="{{logo}}" width="40%" height="60%" /></td>
                <td align=right><b>MEDICAL ORDER SHEET</b></td>
            </tr>
        </table>
        <br/>
        <br/>
        <table width="100%" cellspacing="0" cellpadding="0">
            <tr>
                <td style="border: 1px solid #000; padding: 5px">Inmate Name (SURNAME, First Middle)<br/>
                {{lastname}}, {{firstname}}
                </td>
                <td style="border: 1px solid #000; padding: 5px">OTIS #<br/><br/>
                
                </td>
                <td style="border: 1px solid #000; padding: 5px">DOB <font size=1>(Mmm-dd-yyyy)</font><br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Age&nbsp;&nbsp;
                
                </td> 
            </tr>
            <tr>
                <td colspan="3" height="60" style="border: 1px solid #000; padding: 5px"><b>ALLERGIES:</b><br/><br/><br/></td> 
            </tr>
        </table>
        <table width=100% cellspacing="0" cellpadding="0">
                <tr>
                <td><font size="1"><center><b>START NEW ORDER BELOW</b></center></font></td>
            </tr>
        </table>
        <table width="100%" cellspacing="0" cellpadding="0" style="border: 1px solid #000">
            <tr>
                <td colspan="2" style="border: 1px solid #000; padding: 5px">DATE <font size=1></font> & TIME {{datetime}}<br/><br/>
                <div style="height: 150px">{{order}}</div>
                </td>
            </tr>
            <tr>
                <td width="50%" style="border: 1px solid #000; padding: 5px"></td>
                <td width="50%" style="border: 1px solid #000; padding: 5px"><font size="1"><br/><br/><center>SIGNATURE</center></font></td>
            </tr>            
        </table>
        </div>
        `
    }    




}