import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import { ListItem, Icon, Overlay, ButtonGroup, Button as StyledButton } from 'react-native-elements'
import styles from './Styles'
import {PatientService, MenuService} from './DomainService'
import {Alerts, WebAlert} from './Alerts'
import {Note, Patient} from './Domain'
import { showMessage, hideMessage } from "react-native-flash-message";
import FlashMessage from "react-native-flash-message";
import { StackActions,NavigationActions } from 'react-navigation'
import { AsyncStorage } from "react-native"
import { SideMenu, SideMenuEmbedded } from './SideMenu';
import { HomeScreen } from './HomeScreen';
import { nodeInternals } from 'stack-utils';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import buildOptions from './BuildOptions'
import jsPDF from 'jspdf'
import {MedicalOrderSheet} from './Templates'
import html2pdf from 'html2pdf.js'


  export class NoteDetailScreen extends React.Component {
    constructor(props){
      super(props)

      console.log("props-------------------------")
      console.log(props.navigation.state.params)
      console.log("------------------")
      
      this.quillRef = null;
      this.reactQuillRef = null;
      const action = props.navigation.getParam("action", "")
      const type = props.navigation.getParam("type", "")
      var patientText = props.navigation.getParam("patientName", "")
      var noteText = props.navigation.getParam("text", "")

      var noteId = ""
      if(action == "new" || action == "duplicate")
        noteId = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 10);
      if(action == "edit")
      {
        noteId = props.navigation.getParam("noteId", "")
        let patientService = new PatientService();

        patientService.getNote(noteId).then((note) =>{
          patientService.setNoteOpenDate(note)
        })
      }

      let drawer = Platform.OS === 'web'? true : false;

      this.state = {
        patientText: patientText,
 //       noteText : "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis laoreet ipsum id arcu tincidunt luctus. Mauris et lectus sit amet purus posuere pharetra. Duis vulputate rhoncus tortor, vel feugiat nisi tempus at. Pellentesque massa nunc, accumsan at tempor nec, placerat in diam. Nam ultrices molestie faucibus. Nunc ut metus eget erat interdum sodales. Sed diam urna, cursus mollis enim volutpat, commodo imperdiet ligula. Sed sed lobortis lacus. Proin vel leo nisl. Nulla tempor metus non orci imperdiet, et gravida turpis posuere. Cras porta dictum mauris ac egestas. Integer cursus metus id nunc bibendum, non volutpat diam finibus. Donec dapibus dictum ante, in viverra erat semper tempor. Aenean feugiat turpis sed diam convallis imperdiet. Aliquam pretium et tellus vestibulum viverra. Phasellus quis lacinia augue. Donec viverra nec enim sed placerat. Ut tempor interdum magna et varius. Phasellus ut quam eu massa sagittis facilisis. In turpis odio, tempus eget leo eget, volutpat varius massa. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Pellentesque convallis tempor mi. ",
        noteText : noteText,
        noteInputFocus: false,
        showSuggestion : false,
        selection: {
          start: 0,
          end: 0
        },
        action: action,
        type: type,
        noteId: noteId,
        pageDrawer: drawer,
        keyBuffer: '',
        keyBufferState: 'STOPPED',
        editor: null,
        history: [],
      }

      this.props.navigation.setParams({
        addTextContent: (text) => this._addTextContent(text),
        togglePageDrawer: () => this._togglePageDrawer()
     });
    }

    handleSelectionChange = ({ nativeEvent: { selection } }) => this.setState({ selection })

    htmlHandleSelectionChange(selection, editor){
      if(!this.quillRef) return;
      if(!selection) return;

      //console.log(selection);
      let newSelection = {
        start: selection.index,
        end: selection.length
      }
      this.setState({selection: newSelection, editor: editor})
    }

    static navigationOptions = ({ navigation }) => {
      const {params} = navigation.state || {};
      
      return {
        headerTitle: (
          <View>
            <Text>Patient Note</Text>  
          </View>
        )
        ,
        headerLeft:(
          <View style={{flex: 1, flexDirection: 'row'}}>
            <View style={{position: 'absolute', top: -50, left: 0, right: 0, bottom: 0, justifyContent: 'center', alignItems: 'center', width: Dimensions.get('window').width}}>
              <FlashMessage position="top" style={{justifyContent: 'center', alignItems: 'center', marginTop: 10}} />
            </View>
            <StyledButton   
              icon={{
                name: 'menu',
                size: 15,
                color: '#007AFF'
              }}
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {(
                Platform.OS !== 'web')? 
              navigation.toggleDrawer(): 
              navigation.state.routes[0].params.togglePageDrawer()}} 
              title="Menu" />
              

            <StyledButton   
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {
                
                console.log(navigation); 
                //navigation.push('Home')
                console.log(navigation.reset)

                const resetAction = StackActions.reset({
                  index: 0,
                  actions: [NavigationActions.navigate({ routeName: 'Home' })],
                });
                navigation.dispatch(resetAction);

                }} title="Home" />


            <StyledButton   
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {navigation.goBack()}} title="Back" />

          </View>
        ),
        headerRight: (
          <View style={{flex: 1, flexDirection: 'row'}}>
            <Button onPress={()=>{navigation.state.routes[0].params.saveContent()}} title="Save" />
          </View>
        ),
      };
    };

    _togglePageDrawer() {
      this.setState({pageDrawer: !this.state.pageDrawer})
    }

    _addTextContent(text)
    {
      let result = ""
      if(this.state.keyBufferState === 'STARTED'){
        if(this.quillRef) this.quillRef.insertText(this.state.selection.start-this.state.keyBuffer.length, text)
        else
        {
          result = this.state.noteText.slice(0, this.state.selection.start-this.state.keyBuffer.length-1) 
          + text 
          + this.state.noteText.slice(this.state.selection.start, this.state.noteText.length)
          this.setState({keyBufferState: 'STOPPED', keyBuffer: ''});
          this.setState({'noteText': result})
        }
      }
      else if(this.quillRef) this.quillRef.insertText(this.state.selection.start, text)
      else{        
        result =  this.state.noteText.slice(0, this.state.selection.start) 
        + text + " "
        + this.state.noteText.slice(this.state.selection.start, this.state.noteText.length)
        let selection = this.state.selection
        selection.start += text.length+1
        this.setState({selected: selection})
        this.setState({'noteText': result})
      }
      
    }

    componentDidMount () {
      if(Platform.OS === 'web' && buildOptions.quill === true) this.attachQuillRefs();

      this.keyboardDidShowListener = Keyboard.addListener('keyboardWillShow', () => this._keyboardDidShow());
      this.keyboardDidHideListener = Keyboard.addListener('keyboardWillHide', () => this._keyboardDidHide());
      //this.props.navigation.toggleDrawer()
      this.props.navigation.setParams({ saveContent: () => this.saveNote() });
    }

  
    componentWillUnmount () {
      this.keyboardDidShowListener.remove();
      this.keyboardDidHideListener.remove();
    }
  
    _keyboardDidShow () {
      this.setState({showSuggestion: true})
    }
  
    _keyboardDidHide () {
      this.setState({showSuggestion: false})
    }

    processText(noteText){
      this.setState({noteText})
    }

    attachQuillRefs() {
      // Ensure React-Quill reference is available:
      if (typeof this.reactQuillRef.getEditor !== 'function') return;
      // Skip if Quill reference is defined:
      if (this.quillRef != null) return;
      
      const quillRef = this.reactQuillRef.getEditor();
      if (quillRef != null) this.quillRef = quillRef;
    }

    handleKeyPress = (event) => {
      //console.log(event.key + "|" + this.state.keyBufferState + "|" + this.state.keyBuffer)
      
      if(event.key === 'Escape' && this.state.keyBufferState === 'STARTED'){
        this.setState({keyBufferState: 'STOPPED', keyBuffer: ''});
      }
      else if(event.key === '.'  && this.state.keyBufferState === 'STOPPED'){
        this.setState({keyBufferState: 'STARTED', keyBuffer: ''});
      }
      else if((event.key === ' ' || event.key === 'Enter' || event.key === 'Tab') && this.state.keyBufferState === 'STARTED') {
        this.setState({keyBufferState: 'STOPPED', keyBuffer: ''});
      }
      else if(event.key === 'Backspace' && this.state.keyBufferState === 'STARTED') {
        this.setState({keyBuffer: this.state.keyBuffer.substring(0, this.state.keyBuffer.length - 1)})
        if(this.state.keyBuffer.length - 1 == 0)
          this.setState({keyBufferState: 'STOPPED'})
      }
      else if(this.state.keyBufferState === 'STARTED') {
        this.setState({keyBuffer: this.state.keyBuffer + event.key})
        //.log("Sending characters: " + this.state.keyBuffer + event.key)
      }

      
    }

    saveNote()
    {
      try{
        let patientService = new PatientService();
        let patient = new Patient();
        let note = new Note();

        note.setId(this.state.noteId)
        note.setType(this.state.type)
        note.setText(this.state.noteText)
        patient.setPatientName(this.state.patientText);

        console.log(note)
        console.log(patient)

        patientService.savePatientNote(patient, note)

        showMessage({
          message: "Note Saved",
          type: "info",
          style: {paddingTop: 100},
        });
      }
      catch(e){
        console.log(e)
        if(e == 'PatientNameEmpty') {
          showMessage({
            message: "Cannot save note - patient name is empty",
            type: "danger",
            style: {paddingTop: 100},
          });
        }
    }

    }

    openNote = (note) =>
    {
      console.log(note)
      let patientService = new PatientService();

      patientService.getNote(note.id).then((innerNote) =>{
        patientService.setNoteOpenDate(note)

        
        patientService.getPatientById(innerNote[0].patient).then((p) => {
          this.setState({"action": "edit"})
          this.setState({"type": note.type})
          this.setState({"noteId": note.id})
          this.setState({"noteText": note.text})
          this.setState({patientText: p[0].name})


          
        })
      })
        

      
      
    }
    

    render() {
      return (
        <View style={{flex:1, flexDirection: 'column'}}>
          <View style={{flex:2, flexDirection: 'row'}}>
            {
              this.state.pageDrawer?
              <View style={{flex:2}}>
                <SideMenuEmbedded navigation={this.props.navigation}></SideMenuEmbedded>
              </View>
              :
              <View style={{flex:0}}>

              </View>
            }

            <View style={{flex:4, flexDirection: 'column'}}>
              <StatusBar barStyle="light-content" />
              <ScrollView>
                <View style={{flex:1, flexDirection: "row"}}>
                  <TabComponent currentNoteId={this.state.noteId} noteHandler={this.openNote}/>
                </View>
                <TextInput
                  style={styles.patientText}
                  onChangeText={(patientText) => this.setState({patientText})}
                  value={this.state.patientText}
                  onFocus={() => this.setState({noteInputFocus: false})}
                  placeholder="Patient Name"
                  editable={this.state.action != 'edit'}
                />
                <View style={{justifyContent: "left", alignSelf: 'flex-start', marginLeft: 20}}>
                  <ToolbarComponent date={Date()} patient={this.state.patientText} noteId={this.state.noteId} text={this.state.noteText} textHandler={(text) => this.processText(text)}>                      
                  </ToolbarComponent>
                </View>
                {
                  Platform.OS === 'web' && buildOptions.quill === true?
                  <View style={styles.noteTextWeb}>
                    <ReactQuill theme="snow" 
                      value={this.state.noteText} 
                      onChange={(noteText) => this.processText(noteText)}
                      onKeyDown={this.handleKeyPress}
                      onFocus={() => this.setState({noteInputFocus: true})}
                      onChangeSelection={(range, source, editor) => this.htmlHandleSelectionChange(range, editor)}
                      ref={(el) => { this.reactQuillRef = el }}
                      placeholder="Note Text"
                      />
                    </View>
                    /*                  
                    <div 
                    onChange={(noteText) => this.processText(noteText)}
                    onInput= {(test) => console.log("input" + test)}
                    onKeyDown={this.handleKeyPress}
                    onSelectionChange={this.handleSelectionChange} 
                    onFocus={() => this.setState({noteInputFocus: true})}
                    placeholder="Note Text"
                    ref="noteText"
                    class="editor" 
                      contenteditable="true"> {this.state.noteText}
                    </div>  */                
                  :
                  <TextInput
                  onChangeText={(noteText) => this.processText(noteText)}
                  onKeyPress={this.handleKeyPress}
                  value={this.state.noteText}
                  onSelectionChange={this.handleSelectionChange} 
                  onFocus={() => this.setState({noteInputFocus: true})}
                  style={styles.noteText}
                  multiline={true}
                  placeholder="Note Text"
                  ref="noteText"
                />
                }        


                </ScrollView>                
              </View>
            </View>
          <View>
          {Platform.OS === 'web'? <WebSuggestionComponent  suggestionText={this.state.keyBuffer} next={()=> {return this.refs.noteText}} handler={(text) => this._addTextContent(text)} /> : null }         
          </View>
          {Platform.OS !== 'web'?
          <KeyboardAvoidingView
                      behavior='padding'
                      style={{ backgroundColor: '#eeeeee' }}
                >
                  { this.state.showSuggestion && this.state.noteInputFocus ? <SuggestionComponent  next={()=> {return this.refs.noteText}} handler={(text) => this._addTextContent(text)} /> : null }                
                </KeyboardAvoidingView>
                :
                null
            }
        </View>
      );
    }  
  }

  class ToolbarComponent extends React.Component {
    constructor(props){
      super(props)
      this.state= {
        history: [this.props.text],
        position: 0,
        noteId: this.props.noteId
      }
      this.ready = true
    }

    readyToType() {
      if (this.ready) {
        this.ready = false
        setTimeout(() => { this.ready = true }, 1000)
        return true
      }
      return false
    }

    componentDidUpdate(prevProps) {

      if (prevProps.text !== this.props.text) {
        if(this.props.noteId != this.state.noteId){
          this.setState({noteId: this.props.noteId, history: [this.props.text], position: 0})
          return
        }
        if(this.props.undo === true) return; 
        if(this.state.history && this.props.text === this.state.history[this.state.position]) return
        
        if(!this.readyToType()) return 
        console.log("Saving " + this.props.text)
        let history = this.state.history
        if(history[this.state.position+1])
        {
          history[this.state.position+1] = this.props.text
          this.setState({history: history, position: this.state.position+1})
        }
        else this.setState({history: this.state.history.concat([this.props.text]), position: this.state.position+1})
      }
    }

    undo() {
      let history = this.state.history
      if(this.state.position > 0) history.pop()
      if(this.state.position > 0) this.setState({position: this.state.position - 1, history: history}) 
      if(this.state.position) this.props.textHandler(this.state.history[this.state.position-1])
    }

    saveAsPdf() {
              var doc = new jsPDF('p', 'pt');
              
              doc.setFont('Helvetica Neue')
              
              doc.setFontType("bold");
              doc.setFontSize(12);
              doc.text(20, 40, 'Patient Note')

              doc.setFontType('normal')
              var splitText = doc.splitTextToSize(this.state.history[this.state.position], 500);
              for (var i=0; i<splitText.length; i++){
                doc.text(20, 60, splitText)
              }
              
              
              // Save the Data
              doc.save('Note.pdf')
              this.setState({isVisible: false})
    }

    saveOrderSheetPdf() {
      let orderSheet = new MedicalOrderSheet()
      this.setState({isVisible: false})
      
      html2pdf(orderSheet.generateFromDict({
        datetime: this.props.date.toString(), 
        lastname: this.props.patient.split(/[, ]/)[0], 
        firstname: this.props.patient.split(/[, ]/)[1], 
        order: this.getOrderText(), 
        logo: require("./Assets/ontario.jpg")}))
    }

    getOrderText() {
      const regex = /^[P|p]lan[:]?([\s\S]*?)\n\n/smg;
      let m = regex.exec(this.state.history[this.state.position]);
      if(m && m[1]) {
        return m[1]
      }
      else
        return ""
    }

    showPdfToolbar()
    {
      this.setState({isVisible: true})
    }
    
    render(){
      return (
        <View style={{flex: 1, flexDirection: "row"}}>
        { this.state.isVisible === true &&
        <View>
          <StyledButton title="Save Content as PDF" onPress={() => this.saveAsPdf()}></StyledButton>
          <StyledButton title="Create Medical Order Sheet PDF" onPress={() => this.saveOrderSheetPdf()}></StyledButton>
        </View>
        }
        <Icon
          name='undo'
          type='font-awesome'
          color='#aaa'
          onPress={() => this.undo()}
          />
        <Icon
          name='picture-as-pdf'
          color='#aaa'
          onPress={() => this.showPdfToolbar()}
          />
        </View>
      )
    }
  }

  class TabComponent extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        data: [],
        selectedIndex: -1,
        buttons: []
      }
      this.refreshTabs()
    }

    refreshTabs(){
      let patientService = new PatientService()
      patientService.getLastOpenedNotes().then((notes) => {
        this.setState({data: notes})
        if(this.state.selectedIndex === -1) this.setState({selectedIndex: notes.findIndex((note) => { return note.id === this.props.currentNoteId})})
        this.setState({buttons: notes.filter(item => { return item.text !== undefined}) })
      })
    }

    tabPress(item)
    {
      this.props.noteHandler(item)
      this.refreshTabs()
    }

    updateIndex (selectedIndex) {
      this.tabPress(this.state.buttons[selectedIndex])
      this.setState({selectedIndex: selectedIndex})
    }

    render() {
      return (
        <View style={{flex: 1, flexDirection: 'row', borderRadius: 10, backgroundColor: "#eee"}}>
          { Platform.OS === 'web'?
            <ButtonGroup
                style={{width:'100%'}}
                onPress={(index) => this.updateIndex(index)}
                selectedIndex={this.state.selectedIndex}
                containerStyle={{flex:1}}
                buttons={this.state.buttons.map((item) => { return item.text.replace(/\n/g, ' ').slice(0, 10) + "..." })}
              />
            :
            this.state.data.filter(item => {
              return item.text !== undefined
            }).map((item) => {
                let txt = item.text.slice(0, 10) + "..."
              return <Button title={txt.replace(/\n/g, ' ') + "..."} key={item.id} onPress={() => {this.tabPress(item)}}/>    
            })
          }
        </View>
      )
    }
  }

  class SuggestionComponent extends React.Component {    
    constructor(props){
      super(props)
      
      this.p = new PatientService()
      this.m = new MenuService()
      
      this.state = {
        patientText: "Patient Name",
        suggestionText: "",
        data: [],
        searchMode: false
      }

    }

    componentDidUpdate(prevProps) {
      if (this.props.suggestionText !== prevProps.suggestionText) {
        this.searchText(this.props.suggestionText)
        this.setState({suggestionText: this.props.suggestionText});
      } 
    }

    searchText(suggestionText){
      this.setState({suggestionText})

      if(suggestionText.startsWith(".")){
        this.m.searchPhrasesByShortcut(suggestionText.substring(1)).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: data})       
        })
      }
      else{
        this.p.getNotesByPhrase(suggestionText).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: data})
        })
  
        this.m.searchPhrases(suggestionText).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: this.state.data.concat(data)})       
        })
      }

    }

    toggleSearchmode(){
      if(!this.state.searchMode)
        this.refs.inputSuggestion.focus()
      else{
        console.log(this.props)
        this.props.next().focus()
      }
      this.setState({searchMode: !this.state.searchMode})
    }
    
    listItemPress(text){
      console.log("inserting" + text)
      this.props.handler(text)
      this.setState({suggestionText: '', searchMode: false})
    }

    render(){
      return (<View style={{marginBottom: 70}}>
        { 
          this.state["data"].map(item => {
            return <Button key={item.key} title={item.val.text} onPress={() => {this.listItemPress(item.val.text)}}/>
          })
        }

        <Button title="Search Mode" onPress={()=> {this.toggleSearchmode()}}/>
        <ScrollView  keyboardShouldPersistTaps={'always'}>
        <TextInput
          onChangeText={(suggestionText) => this.searchText(suggestionText)}
          blurOnSubmit={false} 
          onBlur={() => this.props.next().focus()}
          value={this.state.suggestionText}
          ref="inputSuggestion"
        >
        </TextInput>
        </ScrollView>
      </View>)
    }
  }

  class WebSuggestionComponent extends SuggestionComponent {
    constructor(props){
      super(props)
    }

    focus(){
      this.props.next().focus()
      this.searchText('')
    }

    handleKeyPress(event){

    }

    render(){
      return (<View style={{marginBottom: 10, marginLeft: 20, marginRight: 20}}>
        { 
          this.state["data"].map(item => {
            return <Button key={item.key} title={item.val.text} onPress={() => {this.listItemPress(item.val.text)}}/>
          })
        }

        <ScrollView>
          <TextInput
            onChangeText={(suggestionText) => this.searchText(suggestionText)}
            onKeyPress={this.handleKeyPress}
            blurOnSubmit={false} 
            onBlur={() => this.focus()}
            value={this.state.suggestionText}
            ref="inputSuggestion"
            placeholder="Enter text for suggestions"
            style={{fontSize:16, borderWidth: 1, borderColor: '#eeeeee'}}
          >
          </TextInput>
        </ScrollView>
      </View>)
    }

  }


export class PatientsScreen extends React.Component {
    constructor(props){
      super(props)
      let patientService = new PatientService();
      this.state = {
        'data': []
      }
      patientService.getAllPatients().then((patients) => {
        let ps = JSON.parse(patients)

        if(patients && ps)
        {
          let data = ps.map((value, index, array) => {return {"key": String(value.id), "val": {'name': String(value.name)}}})
          if(data)
            this.setState({'data': data})
        }
      });
      

    }
    render() {
      return (
        <View  style={styles.container}>
          <StatusBar barStyle="light-content" />
          <ScrollView style={{flex:1}}>
          <FlatList
              data={this.state.data}
              renderItem={({item}) => 
              <ListItem
                key={item.key}
                title={item.val.name}
                leftIcon={{name: 'account-circle'}}
                onPress={() => {this.props.navigation.navigate('PatientDetail', {"key": item.key, "name": item.val.name})}}
            />
            }
            />
          </ScrollView>
        </View>
      );
    }  
  }
  
  export class PatientDetailScreen extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        'data': [],
        'noteCategoriesVisible': false,
        'patientName': ''
      }
      console.log(this.props.navigation.getParam("key", -1))
      //todo: need to add async then calls
      let patientService = new PatientService();
      let key= parseInt(this.props.navigation.getParam("key", -1));
      let name = this.props.navigation.getParam("name", "")

      patientService.getPatientNotesById(key).then((r,e) =>{
        let notes = r
        if(r){
          notes = notes.map((value, index, array) => {
            let d = new Date(0).toString();
            if(value.updateDate !== undefined)
              d = new Date(value.updateDate).toString();

            return {"key": String(index), "val": {'description': String(value.text), 'patient': value.patient, 'type': value.type, "id": value.id, "patientName": name, "date": d}}
          })
          this.setState({'data': notes})
        }
        
      })
    }

    setNoteCategoriesVisible(state) {
      this.setState({noteCategoriesVisible: state});
    }

    render() {
      return (
        <View  style={styles.container}>
          <StatusBar barStyle="light-content" />
          <ScrollView style={{flex:1}}>

          <FlatList
              data={this.state.data}
              renderItem={({item}) => 
              <ListItem
                style={{backgroundColor: '#ffffff'}}
                key={item.key}
                title={item.val.description}
                subtitle={
                  <View style={{flex: 1}}>
                    <View style={{flex:1, flexDirection: 'row', marginLeft: 10}}>
                      <Text style={styles.ratingText} style={{padding: 2, fontSize: 10}}>{item.val.type}</Text>
                      <Text style={styles.ratingText} style={{padding: 2, fontSize: 10}}>{item.val.date}</Text>
                    </View>
                    <View style={{justifyContent: 'left', paddingTop: 3, flexDirection: 'row'}}>
                    { Platform.OS === 'web'&& <StyledButton leftIcon={{name: 'content-copy'}} small style={{alignSelf:'flex-start'}} title="Duplicate"
                      onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "duplicate", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
                      />}
                    { Platform.OS === 'web'&& <StyledButton leftIcon={{name: 'edit'}} small style={{alignSelf:'flex-start'}} title="Edit Note"
                      onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
                      />}
                    { Platform.OS !== 'web'&& <Button title="Duplicate"
                      onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "duplicate", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
                      />}
                    </View>
                  </View>
                }
                
                onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
              />
              }
            />
          <View style={{justifyContent:'center', flex: 1,  alignItems: 'center', marginTop: 10}}>
          {    
            Platform.OS !== 'web'? 
            <StyledButton
                    icon={{name:'note-add', color:'#ffffff'}}
                    backgroundColor='#03A9F4'
                    buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                    title="Create a New Note For This Patient"
                    onPress={() => new Alerts().noteAlert(this.props.navigation, this.props.navigation.getParam("name", ""))} 
                  />
            :
            <StyledButton
                  icon={{name:'note-add', color:'#ffffff'}}
                  backgroundColor='#03A9F4'
                  buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                  title="Create a New Note For This Patient"
                  onPress={() => this.setState({noteCategoriesVisible: true})} 
                />
            }
            <WebAlert navigation={this.props.navigation} visible={this.state.noteCategoriesVisible} callback={(state) => this.setNoteCategoriesVisible(state)} patientName={this.props.navigation.getParam("name", "")}/>

          
          </View>
          </ScrollView>
        </View>
      );
    }  
  }